import GlobalSettings from '../components/Settings/GlobalSettings'
import { hillo, IKDataEntity } from 'innerken-utils'

const entity = {
  dishId: {
    type: IKDataEntity.Types.Integer,
    displayName: 'ID',
    tableConfig: {
      align: 'start',
    },
  },
  code: {
    displayName: 'code',
  },
  name: {
    displayName: 'name',
  },
  price: {
    displayName: 'price',
  },
  weeklyCount: {
    displayName: 'weeklyCount',
  },
  monthlyCount: {
    displayName: 'monthlyCount',
  },
  totalCount: {
    displayName: 'totalCount',
  },
}
const config = {
  load: async function (filter) {
    return (await hillo.get('BackendData.php', {
      op: 'dishStatistic',
      lang: GlobalSettings.lang(),
      ...filter,
    }))
      .content.map(function (item) {
        return IKDataEntity.parseDataForEntity(item, entity)
      })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
